.ptr-events-wrapper {
  height: 100%;
  width: 100%;
}

.ptr-timeline-content {
  width: 100%;
  z-index: 1000;
  background: rgba(var(--base50rgb), 0.1);
  transform: translate3d(0, 0, 0);
  user-select: none;
}

.ptr-timeline {
  width: 100%;
  height: 100%;
}
.ptr-popup {
  position: fixed;
  background: rgba(var(--base0rgb), 0.95);
  z-index: 99999;
  padding: 0.5rem 0.75rem;
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 0.3rem;
  box-shadow: 1px 3px 7px 3px rgba(0, 0, 0, 0.2);
  font-size: 0.809rem;
  color: var(--base80);
  min-width: 8rem;
  min-height: 2rem;
}

.ptr-popup-header {
  font-size: 1rem;
  font-weight: bold;
  display: flex;
  align-items: center;
  margin-bottom: 0.25rem;
}

.ptr-popup-record-group {
  display: flex;
  align-items: stretch;
  margin: 0.25rem 0 0.5rem;
}

.ptr-popup-record-group:last-child {
  margin-bottom: 0.25rem;
}

.ptr-popup-record-color {
  min-width: 0.35rem;
  margin: 0.1rem 0.5rem 0.1rem 0;
  background: var(--accent50);
}

.ptr-popup-header .ptr-popup-record-color {
  height: 0.75rem;
  width: 0.75rem;
}

.ptr-popup-record {
  flex: 1;
}

.ptr-popup-record-attribute {
  color: var(--base50);
  margin-bottom: 0.15rem;
}

.ptr-popup-record-value-group {
  line-height: 1.15;
}

.ptr-popup-record-value-group .name {
  margin-right: 0.25rem;
}

.ptr-popup-record-value-group .unit {
  margin-left: 0.15rem;
}

/* compressed */
.ptr-popup.compressed .ptr-popup-record {
  display: flex;
  max-width: calc(100% - 1rem);
}

.ptr-popup.compressed .ptr-popup-record-attribute {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  flex-grow: 1;
  padding-right: 0.5rem;
}
.ptr-timeline-year-label {
  stroke: none;
  fill: var(--accent30);
  font-size: 0.85rem;
}

.ptr-timeline-year.background > line {
  stroke: rgba(0, 0, 0, 0.2);
}
.ptr-timeline-month-label {
  stroke: none;
  fill: var(--accent30);
  font-size: 0.618rem;
}
.ptr-timeline-month-label.large {
  font-size: 0.85rem;
}

.ptr-timeline-month.background > line {
  stroke: rgba(0, 0, 0, 0.2);
}
.ptr-timeline-day-label {
  stroke: none;
  fill: var(--accent30);
  font-size: 0.618rem;
}

.ptr-timeline-day.background > line {
  stroke: rgba(0, 0, 0, 0.2);
}
.ptr-timeline-x-titles {
  width: 100%;
  height: 1.75rem;
}
.ptr-timeline-x-titles svg {
  width: 100%;
}

.ptr-timeline-legend-placeholder {
  flex: 0 1 auto;
  display: flex;
  flex-flow: column;
  min-width: 10rem;
}

.ptr-timeline-x-axe .ptr-timeline-content {
  background-color: unset;
}
.ptr-maptimeline {
  display: flex;
}
.ptr-maptimeline .ptr-maptimeline-wrapper {
  display: flex;
  overflow: hidden;
  flex: 1 1 auto;
}

.ptr-maptimeline-scrollable {
  overflow-y: auto;
  overflow-x: hidden;
}

.ptr-timeline-legend-placeholder {
  flex: 0 1 auto;
  display: flex;
  flex-flow: column;
  min-width: 10rem;
}

.ptr-maptimelinelegend {
  flex: 0 1 auto;
  display: flex;
  flex-flow: column;
  min-width: 10rem;
  width: 10rem;
}

.ptr-maptimeline-legenditem {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  padding: 0 1rem;
  font-size: 0.809rem;
  color: var(--base90);
}

.ptr-timeline-tooltip-time {
  white-space: nowrap;
}
.ptr-timeline-tooltip-time :first-child {
  margin-left: 1.5rem;
}

.ptr-timeline-tooltip-layer {
  white-space: nowrap;
  margin: 2px 0;
  display: flex;
  width: 100%;
}

.ptr-timeline-tooltip-layer > div {
  flex-grow: 1;
}

.ptr-timeline-tooltip-layer > div:first-child {
  margin-right: 0.5rem;
  position: relative;
  width: 1rem;
  flex-grow: 0;
}

.ptr-timeline-tooltip-layer span.dot {
  height: 10px;
  width: 10px;
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
}

.ptr-timeline-tooltip-layer > div:nth-child(2) {
  display: flex;
  flex-flow: column;
  font-size: 0.618rem;
  color: var(--base60);
}
.ptr-timeline-tooltip-layer > div:nth-child(2) em {
  color: var(--base100);
  font-size: 0.809rem;
}

.ptr-timeline-x-axe {
  display: flex;
}
.ptr-timeline-overlay-label {
  font-size: 0.8rem;
  fill: rgba(220, 220, 220, 0.9);
}

.ptr-timeline-overlay rect {
  cursor: pointer;
}

.ptr-timeline-overlay text {
  cursor: pointer;
}
.ptr-timeline-periodLimit-limit rect {
  fill: rgba(214, 214, 214, 0.5);
}
.ptr-timeline-picker > rect {
  fill: rgba(253, 65, 8, 0.349);
}

.ptr-timeline-picker > line {
  stroke-width: 1;
  stroke: rgb(224, 0, 0);
}
.ptr-timeline-mouse > rect {
  fill: rgba(76, 177, 204, 0.15);
}

.ptr-timeline-mouse > line {
  stroke-width: 1;
  stroke: rgba(76, 177, 204, 0.7);
}
g.ptr-timeline-year,
.ptr-timeline-year > line {
  stroke-width: 1;
  stroke: var(--accent20);
  fill: var(--base10);
}

.ptr-timeline-year-label {
  stroke: none;
  fill: var(--accent30);
  font-size: 0.85rem;
  font-weight: bold;
}

.ptr-timeline-year.background > line {
  stroke: rgba(0, 0, 0, 0.2);
}
g.ptr-timeline-month,
.ptr-timeline-month > line {
  stroke-width: 1;
  stroke: var(--accent20);
  fill: var(--base10);
}

.ptr-timeline-month-label {
  stroke: none;
  fill: var(--accent30);
  font-size: 0.618rem;
}

.ptr-timeline-month.background > line {
  stroke: rgba(0, 0, 0, 0.2);
}

g.ptr-timeline-month.secondary > line {
  stroke: var(--accent20);
  stroke-dasharray: 2 4;
}
g.ptr-timeline-day,
.ptr-timeline-day > line {
  stroke-width: 1;
  stroke: var(--accent20);
  fill: var(--base10);
}

.ptr-timeline-day.background > line {
  stroke: rgba(0, 0, 0, 0.2);
}